import { useEffect, useState, useRef, RefObject } from "react";


export const useMount = (callback: () => void) => {
  useEffect(() => {
    callback();
  }, []);
};

export const isVoid = (value: unknown) =>
  value === undefined || value === null || value === "";

export const isFalsy = (value:unknown) => (value === 0 ? false : !value);

// 
export const cleanObject = (object:{[key:string]:unknown}) => {
  const result = { ...object };
  Object.keys(result).forEach((key) => {
    const value = result[key];
    if (isVoid(value)) {
      delete result[key];
    }
  });
  return result;
};
export const useDebounce = <V>(value: V, delay?: number) => {
  const [debouncedValue, setDebouncedValue] = useState(value);

  useEffect(() => {
    // value
    const timeout = setTimeout(() => setDebouncedValue(value), delay);
    // useEffect
    return () => clearTimeout(timeout);
  }, [value, delay]);

  return debouncedValue;
};

export const useArray = <T>(value:T[]) => {
  // value:{ name: string; age: number }[] 
  //  
  const [arr,setArr] = useState<typeof value>([])
  useEffect(()=>{
    setArr(value);
  },[])
  //  
//   const [arr,setArr] = useState(value)
  //  
  return (
    {
        arr,
        add : (newProp:T)=> setArr([...arr,newProp]),
        clear: ()=> setArr([]),
        removeIndex : (index:number) => {
            const copy = [...arr]
            copy.splice(index,1)
            setArr(copy)
            // oldway and incorrect : setArr(arr.splice(index,1)) 
        }
    }
  )
  
};

export const useMountedRef = () => {
  const mountedRef = useRef(false);

  useEffect(() => {
    mountedRef.current = true;
    return () => {
      mountedRef.current = false;
    };
  });

  return mountedRef;
};

const isError = (value:any): value is Error => value?.message
export const errorBox = ({error}:{error:unknown}) =>{
  if(isError(error)){
    return error.message
  }
  return null;
}

export const useTitle = (title : string , keepOnMount: boolean = true) =>{
  const oldTitle = document.title;
  // const oldTitle = useRef(document.title).current
  //   oldTitle = React App
  //  oldTitle = title

  useEffect(()=>{
    document.title=title
  },[title])

  useEffect(()=>{
    return () =>{
      if(!keepOnMount){
        //  
        // console.log("",oldTitle)
        //  1.  2. function update 3. useRef
        document.title = oldTitle
      }
    }
  },[keepOnMount,oldTitle])
}

export const useClickOutside= (ref: RefObject<HTMLElement>,parent:RefObject<HTMLElement>, handler: Function) =>{
  useEffect(() => {
      const listener = (event: MouseEvent) => {
          if(parent && parent.current && parent.current.contains(event.target as HTMLElement)  ){
            // 
            return
          }
          if(!ref.current || ref.current.contains(event.target as HTMLElement) ){
            return
          }
          handler(event)
      }
      document.addEventListener('click',listener)
      return () => {
          document.removeEventListener('click', listener)
      }
  }, [ref,parent, handler])
}  