import { useCallback, useState, useReducer } from "react";
import {useMountedRef} from 'utils'
//&loading
// .. code
interface State<D> {
  error: string | null;
  data: D | null;
  stat: "idle" | "loading" | "error" | "success";
}

const defaultInitialState: State<null> = {
  stat: "idle",
  data: null,
  error: null,
};
const defaultConfig = {
  throwError: false
}

const useSafePatch = <T>(dispatch:(...args: T[])=> void) =>{
  const mountedRef = useMountedRef()
  return useCallback((...args:T[])=> mountedRef.current? dispatch(...args): void 0,[
    mountedRef,dispatch
  ])

}
//  ?
// 
export const useAsync = <D>(initialState?: State<D>, iniConfig?: typeof defaultConfig) => {
  const config = {...defaultConfig,...iniConfig}
  // action vs dispatch type
  const [state, dispatch] = useReducer((state:State<D>,action:Partial<State<D>>)=>({
    ...state,...action
  }),{
    ...defaultInitialState,
    ...initialState,
  });
  const safeDispatch = useSafePatch(dispatch)

  const [retry, setRetry] = useState(() => () => {});
  const setData = useCallback((data: D) =>{
    safeDispatch({
      data,
      stat: "success",
      error: null,
  })
  },[safeDispatch])

  const setError = useCallback((error: string) =>{
    safeDispatch({
      error,
      stat: "error",
      data: null,
  })
  },[safeDispatch])

  const run = useCallback((promise: Promise<D> ,
    runConfig?: { retry: () => Promise<D> }) => {
    if (!promise || !promise.then) {
      throw new Error(" Promise ");
    }
    setRetry(() => () => {
      // run(promise)  return XXX // 
      if (runConfig?.retry) {
        run(runConfig?.retry(), runConfig);
        console.log(runConfig,promise)
      }
    });
    // setState  => render
    // setState({ ...state, stat: "loading" });
    dispatch({stat:"loading"})
    return promise
      .then((data) => {
        setData(data);
        return data;
      })
      .catch((error) => {
        setError(error);
        if(config.throwError) return Promise.reject(error); // try catch
        return error;
      });
      // state
  } ,[setData,setError,config.throwError,safeDispatch])

  return {
    isIdle: state.stat === "idle",
    isLoading: state.stat === "loading",
    isError: state.stat === "error",
    isSuccess: state.stat === "success",
    run,
    setData,
    setError,
    retry,
    ...state
  };
};